import { JetView } from "webix-jet";
import { createState } from "jet-restate";

import phone from "../../libs/phone";

export default class CallPanel extends JetView {
	config() {
		this.time = 0;
		this.Helpers = this.app.getService("helpers");
		this.Back = this.app.getService("backend");
		this.callStatuses = this.Back.callStatuses;
		this.State = this.getParam("state", true);
		this.LocalState = createState({
			audio: true,
			video: false,
			remoteVideo: false,
			remoteAudio: false,
		});
		this.Initiator = this.State.callUsers[0] === this.app.config.user;

		if (this.State.callStatus === this.callStatuses["active"]) {
			// loading page while call already active
			// most probably due to page reloading
			// we need to force user to interact with page, or players will not start
			this.AfterRefresh = true;
		}

		const users = this.app.getService("local")._users;

		return {
			type: "clean",
			width: this.getParam("compact", true) ? 0 : 380,
			rows: [
				{
					css: "webix_chat_call",
					localId: "fullscreen",
					rows: [
						{
							animate: false,
							borderless: true,
							cells: [this.GetAudioConfig(users), this.GetVideoConfig()],
						},
						{
							borderless: true,
							css: "webix_chat_video webix_chat_audio",
							minHeight: 96,
							gravity: 0.0001,
							template: `
								<div style="position: relative; width: 100%; height: 100%;">
									<div class="webix_chat_video_remote">
										<audio autoplay></audio>
										<video autoplay playsinline></video>
									</div>
									<div class="webix_chat_video_local">
										<audio autoplay></audio>
										<video autoplay playsinline></video>
									</div>
									${this.Helpers.avatar(
										users.getItem(this.app.config.user),
										"webix_chat_video_local_avatar"
									)}
								</div>
							`,
							localId: "connection",
						},
						this.GetActionsConfig(),
					],
				},
			],
		};
	}

	init() {
		const video = this.$$("connection").$view;
		this.localVideo = video.querySelector(".webix_chat_video_local");
		this.localAvatar = video.querySelector(".webix_chat_video_local_avatar");
		this.remoteVideo = video.querySelector(".webix_chat_video_remote");

		this.on(this.Back.pubsub(), "signal", ({ msg, type }) => {
			// do nothing, until user confirms that they want to join the call
			if (this.AfterRefresh) return;

			if (msg) msg = JSON.parse(msg);
			if (this.Phone) this.Phone.events(type, msg);
		});

		this.on(this.app, "updateCallTime", time => this.UpdateCallTime(time));

		this.on(this.State.$changes, "callStatus", status =>
			this.HandleCallStatusChange(status)
		);

		this.on(this.LocalState.$changes, "remoteVideo", () => this.ShowVideo());
	}

	GetAudioConfig(users) {
		const _ = this.app.getService("locale")._;
		return {
			localId: "audio",
			type: "clean",
			rows: [
				{
					css: "webix_chat_audio_time",
					localId: "audio-timer",
					batch: "active",
					height: 56,
					template: () => {
						return `${_("Active call")} (${this.Helpers.normalizeTime(
							this.time
						)})`;
					},
				},
				{
					gravity: 0.0001,
					css: "webix_chat_audio",
				},
				{
					height: 270,
					css: "webix_chat_audio",
					template: () => {
						const user = users.getItem(
							this.State.callUsers[this.Initiator ? 1 : 0]
						);
						const avatar = this.Helpers.avatar(
							user,
							"webix_chat_call_avatar webix_chat_avatar"
						);
						return (
							avatar + "<p class='webix_chat_call_name'>" + user.name + "</p>"
						);
					},
				},
				{
					batch: "outgoing",
					css: "webix_chat_audio",
					height: 30,
					template: _("Ringing"),
				},
				{
					batch: "income",
					css: "webix_chat_audio",
					height: 30,
					template: _("Is calling you"),
				},
				{
					batch: "income",
					height: 42,
					css: "webix_chat_audio",
					cols: [
						{},
						{
							view: "button",
							height: 0,
							width: 86,
							value: _("Accept"),
							css: "webix_chat_accept",
							click: () => this.AcceptCall(),
						},
						{
							view: "button",
							width: 86,
							value: _("Reject"),
							css: "webix_danger",
							click: () => this.RejectCall(),
						},
						{},
					],
				},
				{
					batch: "refresh",
					height: 42,
					css: "webix_chat_audio",
					cols: [
						{},
						{
							view: "button",
							height: 0,
							width: 86,
							value: _("Join call"),
							css: "webix_chat_accept",
							click: () => {
								this.AfterRefresh = false;
								this.Start(true);
							},
						},
						{
							view: "button",
							width: 86,
							value: _("End call"),
							css: "webix_danger",
							click: () => this.EndCall(),
						},
						{},
					],
				},
				{
					gravity: 0.0001,
					css: "webix_chat_audio",
				},
			],
		};
	}

	GetVideoConfig() {
		return {
			localId: "video",
			type: "clean",
			rows: [
				{
					css: "webix_chat_video_time",
					height: 56,
					type: "clean",
					cols: [
						{
							localId: "video-timer",
							template: () => {
								return this.Helpers.normalizeTime(this.time);
							},
						},
						{
							view: "icon",
							localId: "fullscreen-icon",
							icon: "chi-fullscreen",
							click: () => {
								this.SetFullscreen(!this.fullscreen);
							},
						},
						{ width: 6 },
					],
				},
			],
		};
	}

	GetActionsConfig() {
		return {
			height: 80,
			css: "webix_chat_call_actions",
			localId: "call-actions",
			rows: [
				{},
				{
					height: 44,
					cols: [
						{},
						{
							view: "toggle",
							type: "icon",
							offIcon: "chi-video-off",
							onIcon: "chi-video",
							localId: "video-toggle",
							css: "webix_primary",
							width: 44,
							click: () => this.EnableVideo(true),
						},
						{ width: 32 },
						{
							view: "toggle",
							type: "icon",
							offIcon: "chi-audio-off",
							onIcon: "chi-audio",
							localId: "audio-toggle",
							css: "webix_primary",
							width: 44,
							value: true,
							click: () => this.EnableAudio(true),
						},
						{ width: 32 },
						{
							view: "button",
							type: "icon",
							icon: "chi-hangup",
							css: "webix_danger",
							width: 44,
							click: () => this.EndCall(),
						},
						{},
					],
				},
				{},
			],
		};
	}

	ControlVisibility(type, batch, actions) {
		const view = this.$$(type);
		view.show();
		if (batch) view.showBatch(batch);

		this.$$("call-actions")[actions ? "show" : "hide"]();
	}

	SetFullscreen(fullscreen) {
		webix.fullscreen.exit();

		const icon = this.$$("fullscreen-icon");
		icon.config.icon = "chi-fullscreen" + (fullscreen ? "-off" : "");
		icon.refresh();

		this.fullscreen = fullscreen
			? webix.fullscreen.set(this.$$("fullscreen"), {
					head: false,
					css: "webix_chat_video_fullscreen",
			  })
			: null;
	}

	Start(isAfterRefresh) {
		if (this.AfterRefresh) {
			this.ControlVisibility("audio", "refresh", false);
		} else {
			this.ControlVisibility("audio", "active", true);
			this.InitPhone(this.Initiator, isAfterRefresh);

			if (this.LocalState.audio) this.EnableAudio();

			if (this.LocalState.video) this.EnableVideo();
		}
	}

	InitPhone(isInitiator, isAfterRefresh) {
		const alist = this.$$("connection").$view.querySelectorAll("audio");
		const vlist = this.$$("connection").$view.querySelectorAll("video");
		this.Phone = phone(
			this.SendSignal.bind(this),
			this.app.config.rtcConfig || null,
			{
				local: { audio: alist[1], video: vlist[1] },
				remote: { audio: alist[0], video: vlist[0] },
				active: {
					remote: {
						video: v => (this.LocalState.remoteVideo = v),
						audio: v => (this.LocalState.remoteAudio = v),
					},
				},
				end: () => this.EndCall(),
			},
			isInitiator ? (isAfterRefresh ? 3 : 1) : isAfterRefresh ? 2 : 4,
			{ video: this.LocalState.video, audio: this.LocalState.audio },
			this.app.getService("locale")._
		);
	}

	EndCall() {
		this.SetFullscreen(false);
		this.getParam("state", true).callStatus = this.callStatuses["drop"];

		if (this.Phone) {
			this.Phone.end();
			this.Phone = null;
			this.Back.updateCall(this.State.callId, this.callStatuses["end"]);
		} else {
			this.Back.updateCall(this.State.callId, this.callStatuses["ignore"]);
		}
	}

	AcceptCall() {
		this.Back.updateCall(this.State.callId, this.callStatuses["accept"]);
	}

	RejectCall() {
		this.getParam("state", true).callStatus = this.callStatuses["drop"];
		this.Back.updateCall(this.State.callId, this.callStatuses["reject"]);
	}

	SendSignal(type, payload) {
		// rtc handler can trigger callback when view already destroyed
		if (this.app) {
			if (typeof payload !== "string") payload = JSON.stringify(payload);
			return this.Back.signalCall(type, payload);
		}
	}

	EnableAudio(toggle) {
		const v = toggle
			? (this.LocalState.audio = !this.LocalState.audio)
			: this.LocalState.audio;
		if (this.Phone) {
			this.Phone.enable("audio", v).catch(() => {
				this.LocalState.audio = false;
				this.$$("audio-toggle").setValue(false);
			});
		}
	}

	EnableVideo(toggle) {
		const v = toggle
			? (this.LocalState.video = !this.LocalState.video)
			: this.LocalState.video;
		if (this.Phone) {
			this.Phone.enable("video", v)
				.catch(() => {
					this.LocalState.video = false;
					this.$$("video-toggle").setValue(false);
				})
				.finally(() => {
					this.ShowVideo();
				});
		}
	}

	CheckVideoVisibility() {
		const remote = this.LocalState.remoteVideo;
		const local = this.LocalState.video;

		let visibility = ["none", "none", "none"];
		if (remote) {
			if (local) visibility = ["none", "block", "block"];
			else visibility = ["block", "none", "block"];
		} else if (local) visibility = ["none", "block", "none"];

		[this.localAvatar, this.localVideo, this.remoteVideo].forEach((node, i) => {
			node.style.display = visibility[i];
		});
	}

	UpdateCallTime(time) {
		this.time = time;

		if (this.fullscreen)
			this.fullscreen.queryView({ localId: "video-timer" }).refresh();
		else if (this.$$("audio-timer").isVisible())
			this.$$("audio-timer").refresh();
		else if (this.$$("video-timer").isVisible())
			this.$$("video-timer").refresh();
	}

	HandleCallStatusChange(status) {
		if (status == this.callStatuses["init"]) {
			this.ControlVisibility(
				"audio",
				this.Initiator ? "outgoing" : "income",
				this.Initiator
			);
		} else if (status == this.callStatuses["active"]) {
			this.Start();
		} else if (
			status == this.callStatuses["reject"] ||
			status == this.callStatuses["end"] ||
			status == this.callStatuses["ignore"] ||
			status == this.callStatuses["lost"]
		) {
			if (this.Phone) {
				this.SetFullscreen(false);
				this.Phone.end();
				this.Phone = null;
			}
		}
	}

	ShowVideo() {
		if (
			this.State.callStatus == this.callStatuses["active"] &&
			!this.AfterRefresh
		) {
			const remote = this.LocalState.remoteVideo;
			const audio = !this.LocalState.video && !remote;

			if (audio || !remote) {
				this.SetFullscreen(false);
				this.ControlVisibility("audio", "active", true);
			} else if (!this.fullscreen) this.ControlVisibility("video", "", true);

			this.CheckVideoVisibility();
		}
	}
}
