export default class UploadHandler {
	constructor(app) {
		this.app = app;
		this.initUploader();
	}

	initUploader() {
		this.uploader = webix.ui({
			view: "uploader",
			apiOnly: true,
			accept: "image/*",
			autosend: true,
			on: {
				onBeforeFileAdd: () => this.configUpload(),
			},
		});
	}

	config(props) {
		webix.extend(this.uploader.config, props, true);
	}

	getUploader() {
		return this.uploader;
	}

	configUpload() {
		this.uploader.config.upload = this.app
			.getService("backend")
			.avatarUploadUrl();
	}

	fileDialog() {
		this.uploader.fileDialog();
	}
}
